@import '../utils/mixins';

body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f2f2f2;
}

.home {
  //display: flex; 
  //flex-wrap: wrap;
  max-width: 1200px;
  margin: 0 auto;
  background-color: #fff;
  padding: 20px;
  border: 1px solid #ddd;
}

.left-section {
  //display: flex;
  //flex-direction: column;
  padding:  0 50px;
  
  width: 335px;
  height: 628px;
}

.categories-title{
  color: #4f2ee8;
  font-size: 18px;
  font-weight: 700;
  letter-spacing: 2%;
}

::-webkit-scrollbar {
  width: 8px;
  height: 167px;
}

/* Track */
::-webkit-scrollbar-track {
  border-radius: 8px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #E0E0E0; 
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: e9e9e0; 
}




.categories-block {
  overflow-y: scroll;
  width: 356px;
  height: 472px;
  
}

.categories-block li{
  color: #202024;
  font-size: 18px;
  list-style: none;
  line-height: 32px;
  padding:18px 0;
  cursor: pointer;
}

.categories-block li:hover{
  color: var(--violet);
  font-size: 18px;
  list-style: none;
  line-height: 32px;
  padding:18px 0;
  cursor: pointer;
}

.charities-slider-block {
  padding: 142px 20px 0 20px;
  width: 356px;
}


//best sellers books title
.main-title{
  
  white-space: nowrap;
  color: black;
  width: 360px;
  font-size: 32px;
  font-weight: 700;
  letter-spacing: -4%;
  line-height: 38px;
  text-align: center;
  padding: 0 0 40px 80px;
}
.books {
    color: #4f2ee8;
}


//usunąłem stylowanie .block-list 


.image-container {
  display: flex;
  flex-wrap: nowrap;
  overflow: hidden;
  list-style: none;
}


.category-list {
display: flex;
list-style: none;
overflow: hidden;
}

.image-container {
  // Dla tabletów (szerokość powyżej 768px)
  @include tablet {
    width: 218px; /* Szerokość obrazków dla tabletów */
  }

  // Dla ekranów desktop (szerokość powyżej 1200px)
  @include desktop {
    width: 180px; /* Szerokość obrazków dla ekranów desktop */
  }

  // Dla innych przypadków (domyślnie)
  width: 335px; /* Domyślna szerokość obrazków */
}